.wave {
    opacity: .2;
    position: absolute;
    top: 15%;
    left: 0%;
    background: -webkit-gradient(linear, left top, right top, from(#b2ffb2), to(#6affd3));
    background: linear-gradient(to right, #b2ffb2 0%, #6affd3 100%);
    width: 1600px;
    height: 1600px;
    margin-left: -165px;
    margin-top: 420px;
    -webkit-transform-origin: 46% 48%;
            transform-origin: 46% 48%;
    border-radius: 40%;
    -webkit-animation: drift 30000ms infinite linear;
            animation: drift 30000ms infinite linear;
}

.wave.-three {
    -webkit-animation: drift 28000ms infinite linear;
            animation: drift 28000ms infinite linear;
}

